<template>
  <button type="button" class="btn" :class='[computedType, computedSize, computedOutline, computedActive, computedBlock]'>{{ btnText }}</button>
</template>

<script>
export default {
  name: 'ImageButton',
  props: {
    btnText: {
      type: String,
      default: ''
    },
    btnType: {
      type: String,
      default: ''
    },
    btnSize: {
      type: String,
      default: ''
    },
    btnOutline: {
      type: String,
      default: ''
    },
    btnActive: {},
    btnBlock: {}
  },
  computed: {
    // 控制按钮的类型
    computedType: function() {
      return this.btnType ? `btn-${this.btnType}` : ''
    },

    // 控制按钮大小尺寸
    computedSize: function() {
      return this.btnSize ? `btn-${this.btnSize}` : ''
    },

    // 控制按钮边框
    computedOutline: function() {
      return this.btnOutline ? `btn-outline-${this.btnOutline}` : ''
    },

    // 控制按钮是否激活
    computedActive: function() {
      return this.btnActive === 'true' ? 'active' : ''
    },

    // 控制按钮是否是块元素
    computedBlock: function() {
      return this.btnBlock === 'true' ? 'btn-block' : ''
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.btn {
  display: inline-block;
  font-weight: 400;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  border: 1px solid transparent;
  padding: 0.375rem 0.75rem;
  font-size: 1rem;
  line-height: 1.5;
  border-radius: 0.25rem;
  transition: background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}

.btn:focus, .btn:hover {
  text-decoration: none;
}

.btn:focus, .btn.focus {
  outline: 0;
  box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25);
}

.btn.disabled, .btn:disabled {
  opacity: .65;
}

.btn:not([disabled]):not(.disabled):active, .btn:not([disabled]):not(.disabled).active {
  background-image: none;
}

.btn-primary {
  padding: 0;
  font-size: 1.25rem;
  line-height: 1.5;
  border-width: 2px;
  border-radius: 0.6rem;
  color: #fff;
  background-color: #007bff;
  border-color: #0062cc;
}

.btn-primary:hover {
  border-width: 6px;
  color: #fff;
  background-color: #0069d9;
  border-color: #007bff;
}

.btn-primary:focus, .btn-primary.focus {
  box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.5);
}

.btn-primary.disabled, .btn-primary:disabled {
  background-color: #007bff;
  border-color: #007bff;
}

.btn-primary:not([disabled]):not(.disabled):active, .btn-primary:not([disabled]):not(.disabled).active,
.show>.btn-primary.dropdown-toggle {
  color: #fff;
  background-color: #0062cc;
  border-color: #005cbf;
  box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.5);
}
</style>



