import Vue from 'vue'
import Router from 'vue-router'
const require_view = require('./_import_' + process.env.NODE_ENV)

Vue.use(Router)

import Layout from '@/views/layout'

/**
 * hidden: true                   if `hidden:true` will not show in the sidebar(default is false)
 * name:'router-name'             the name is used by <keep-alive> (must set!!!)
 **/
export const constantRouterMap = [
  {
    path: '',
    component: Layout,
    redirect: '/home',
    children: [{
      name: 'home',
      path: 'home',
      component: require_view('home/index'),
    }]
  },
  {
    path: '',
    component: Layout,
    children: [{
      name: 'reserved',
      path: 'reserved',
      component: require_view('reserved/index'),
    }]
  },
  {
    path: 'extension',
    component: Layout,
    children: [{
      name: 'extension',
      path: '',
      component: require_view('extension/index'),
    }]
  },
  {
    path: 'extension',
    component: Layout,
    children: [{
      name: 'extension/extend',
      path: 'extend',
      component: require_view('extension/extend/index'),
    }]
  },
  {
    path: 'extension',
    component: Layout,
    children: [{
      name: 'extension/order',
      path: 'order',
      component: require_view('extension/order/index'),
    }]
  },
  {
    path: 'authorize',
    component: Layout,
    children: [{
      name: 'password',
      path: 'password',
      component: require_view('authorize/password/index'),
    }]
  },
  {
    path: 'authorize',
    component: Layout,
    children: [{
      name: 'id_card',
      path: 'id_card',
      component: require_view('authorize/id_card/index'),
    }]
  },
  {
    path: 'select',
    component: Layout,
    children: [{
      name: 'date',
      path: 'date',
      component: require_view('select/date/index'),
    }]
  },
  {
    path: 'select',
    component: Layout,
    children: [{
      name: 'room',
      path: 'room',
      component: require_view('select/room/index'),
    }]
  },
  {
    path: 'order',
    component: Layout,
    children: [{
      name: 'order',
      path: '',
      component: require_view('order/index'),
    }]
  },
  {
    path: 'order',
    component: Layout,
    children: [{
      name: 'number_of_people',
      path: 'number_of_people',
      component: require_view('order/number_of_people/index'),
    }]
  },
  {
    path: 'payment',
    component: Layout,
    children: [{
      name: 'payment',
      path: '',
      component: require_view('payment/index'),
    }]
  },
  {
    path: 'payment',
    component: Layout,
    children: [{
      name: 'payment/alipay',
      path: 'alipay',
      component: require_view('payment/alipay/index'),
    }]
  },
  {
    path: 'payment',
    component: Layout,
    children: [{
      name: 'payment/weixin',
      path: 'weixin',
      component: require_view('payment/weixin/index'),
    }]
  },
  {
    path: 'payment',
    component: Layout,
    children: [{
      name: 'payment/member',
      path: 'member',
      component: require_view('payment/member/index'),
    }]
  },
  {
    path: 'checkout',
    component: Layout,
    children: [{
      name: 'checkout',
      path: '',
      component: require_view('checkout/index'),
    }]
  },
  {
    path: 'checkout',
    component: Layout,
    children: [{
      name: 'checkout/bill',
      path: 'bill',
      component: require_view('checkout/bill/index'),
    }]
  },
  {
    path: 'checkout',
    component: Layout,
    children: [{
      name: 'checkout/review',
      path: 'review',
      component: require_view('checkout/review/index'),
    }]
  },
  {
    name: 'about',
    path: '/about',
    component: require_view('about/index')
  },
  { path: '/404', component: require_view('error/404'), hidden: true },
  { path: '/401', component: require_view('error/401'), hidden: true },
  { path: '*', redirect: '/404', hidden: true }
]

export default new Router({
  // mode: 'history', // require service support
  scrollBehavior: () => ({ y: 0 }),
  routes: constantRouterMap
})



