<template>
  <div class="container">
    <el-row type="flex">
      <el-col :span="4">
        <div class="left_container">
          <div class="logo">
            <!--<img :src="img_logo" alt="logo">-->
          </div>
        </div>
      </el-col>
      <el-col :span="20">
      </el-col>
    </el-row>
  </div>
</template>

<script>
import img_logo from '@/assets/logo/logo_color_120x44dp.png'

export default {
  data() {
    return {
      img_logo
    };
  }
}
</script>

<style scoped>
.container {
  height: 120px;
}

.left_container {
  display: flex;
  height: 100%;
}

.logo {
  margin-left: 8px;
  display: flex;
  flex-direction: column-reverse;
}

.logo img {
  width: 120px;
  height: 44px;
}
</style>



